"use client"

import { useState, useEffect } from "react"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { toast } from "sonner"
import { LogIn, LogOut, Clock, History, Loader2, QrCode } from "lucide-react"

const API_BASE_URL = process.env.NEXT_PUBLIC_API_BASE_URL || "https://localhost:7099/api"

export function StudentDashboard() {
    const [isLoading, setIsLoading] = useState(true)
    const [isSubmitting, setIsSubmitting] = useState(false)
    const [user, setUser] = useState<any>(null)
    const [activeSession, setActiveSession] = useState<any>(null)
    const [history, setHistory] = useState<any[]>([])

    // Form state
    const [purpose, setPurpose] = useState("")
    const [department, setDepartment] = useState("")

    useEffect(() => {
        const storedUser = localStorage.getItem("user")
        if (storedUser) {
            const parsed = JSON.parse(storedUser)
            setUser(parsed)
            fetchData(parsed.email)
        }
    }, [])

    const fetchData = async (email: string) => {
        setIsLoading(true)
        try {
            const token = localStorage.getItem("token")
            const res = await fetch(`${API_BASE_URL}/visitors`, {
                headers: { "Authorization": `Bearer ${token}` }
            })
            if (res.ok) {
                const allVisitors = await res.json()
                const myVisits = allVisitors.filter((v: any) => v.full_Name === user?.fullName || v.full_Name === user?.username)
                // Note: In a real system, we'd filter by UserId or Email on the backend

                setHistory(myVisits.slice(0, 5))
                const active = myVisits.find((v: any) => !v.time_out || v.time_out.startsWith("00:00:00"))
                setActiveSession(active)
            }
        } catch (err) {
            console.error("Failed to fetch student data:", err)
        } finally {
            setIsLoading(false)
        }
    }

    const handleCheckIn = async (e: React.FormEvent) => {
        e.preventDefault()
        if (!purpose || !department) {
            toast.error("Please fill in all fields")
            return
        }

        setIsSubmitting(true)
        try {
            const token = localStorage.getItem("token")
            const res = await fetch(`${API_BASE_URL}/visitors`, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                    "Authorization": `Bearer ${token}`
                },
                body: JSON.stringify({
                    full_Name: user?.fullName || user?.username,
                    program: "Student", // Or fetch from user profile if available
                    student_Year: "N/A",
                    purpose,
                    department
                })
            })

            if (res.ok) {
                toast.success("Checked in successfully!")
                fetchData(user.email)
                setPurpose("")
                setDepartment("")
            } else {
                toast.error("Failed to check in")
            }
        } catch (err) {
            toast.error("Connection error")
        } finally {
            setIsSubmitting(false)
        }
    }

    const handleCheckOut = async () => {
        if (!activeSession) return
        setIsSubmitting(true)
        try {
            const token = localStorage.getItem("token")
            const res = await fetch(`${API_BASE_URL}/visitors/checkout/${activeSession.id}`, {
                method: "PUT",
                headers: { "Authorization": `Bearer ${token}` }
            })

            if (res.ok) {
                toast.success("Checked out successfully!")
                fetchData(user.email)
            } else {
                toast.error("Failed to check out")
            }
        } catch (err) {
            toast.error("Connection error")
        } finally {
            setIsSubmitting(false)
        }
    }

    if (isLoading) {
        return (
            <div className="flex h-[400px] items-center justify-center">
                <Loader2 className="h-8 w-8 animate-spin text-primary" />
            </div>
        )
    }

    return (
        <div className="max-w-4xl mx-auto space-y-6">
            <div className="flex items-center justify-between">
                <div>
                    <h1 className="text-3xl font-bold text-foreground">Student Portal</h1>
                    <p className="text-muted-foreground">Welcome, {user?.fullName || user?.username}</p>
                </div>
            </div>

            <div className="grid gap-6 md:grid-cols-2">
                {/* Check-in / Active Status */}
                <Card className="border-border bg-card shadow-lg">
                    <CardHeader>
                        <CardTitle className="flex items-center gap-2">
                            {activeSession ? <Clock className="h-5 w-5 text-emerald-500" /> : <LogIn className="h-5 w-5 text-primary" />}
                            {activeSession ? "Current Session" : "New Check-in"}
                        </CardTitle>
                        <CardDescription>
                            {activeSession ? "You are currently checked in." : "Fill in the details to start your visit."}
                        </CardDescription>
                    </CardHeader>
                    <CardContent>
                        {activeSession ? (
                            <div className="space-y-4">
                                <div className="p-4 rounded-lg bg-emerald-500/10 border border-emerald-500/20">
                                    <div className="flex justify-between text-sm mb-1">
                                        <span className="text-muted-foreground">Purpose:</span>
                                        <span className="font-medium">{activeSession.purpose}</span>
                                    </div>
                                    <div className="flex justify-between text-sm mb-1">
                                        <span className="text-muted-foreground">Department:</span>
                                        <span className="font-medium">{activeSession.department || "General"}</span>
                                    </div>
                                    <div className="flex justify-between text-sm">
                                        <span className="text-muted-foreground">Started at:</span>
                                        <span className="font-medium">{new Date(activeSession.visiteDate).toLocaleTimeString()}</span>
                                    </div>
                                </div>

                                {activeSession.visitToken && (
                                    <div className="flex flex-col items-center gap-2 p-4 bg-white rounded-lg">
                                        <p className="text-xs text-slate-500 uppercase font-bold">Your Digital Pass</p>
                                        <div className="p-2 border-2 border-slate-100 rounded-lg">
                                            <QrCode className="h-24 w-24 text-slate-900" />
                                        </div>
                                        <p className="text-[10px] text-slate-400">Show this at the exit</p>
                                    </div>
                                )}

                                <Button
                                    className="w-full bg-emerald-600 hover:bg-emerald-700 h-12 text-lg"
                                    onClick={handleCheckOut}
                                    disabled={isSubmitting}
                                >
                                    {isSubmitting ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <LogOut className="mr-2 h-5 w-5" />}
                                    Check Out Now
                                </Button>
                            </div>
                        ) : (
                            <form onSubmit={handleCheckIn} className="space-y-4">
                                <div className="space-y-2">
                                    <Label htmlFor="purpose">Purpose of Visit</Label>
                                    <Input
                                        id="purpose"
                                        placeholder="e.g. Library, Registrar, Inquiry"
                                        value={purpose}
                                        onChange={(e) => setPurpose(e.target.value)}
                                        className="h-11"
                                    />
                                </div>
                                <div className="space-y-2">
                                    <Label htmlFor="dept">Department</Label>
                                    <Input
                                        id="dept"
                                        placeholder="e.g. ICT Office, Accounting"
                                        value={department}
                                        onChange={(e) => setDepartment(e.target.value)}
                                        className="h-11"
                                    />
                                </div>
                                <Button
                                    type="submit"
                                    className="w-full h-11 text-lg"
                                    disabled={isSubmitting}
                                >
                                    {isSubmitting ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <LogIn className="mr-2 h-4 w-4" />}
                                    Check In
                                </Button>
                            </form>
                        )}
                    </CardContent>
                </Card>

                {/* Quick History */}
                <Card className="border-border bg-card">
                    <CardHeader>
                        <CardTitle className="flex items-center gap-2">
                            <History className="h-5 w-5 text-primary" />
                            Recent Visits
                        </CardTitle>
                        <CardDescription>Your last 5 sessions</CardDescription>
                    </CardHeader>
                    <CardContent>
                        <div className="space-y-3">
                            {history.length > 0 ? (
                                history.map((visit) => (
                                    <div key={visit.id} className="flex items-center justify-between p-3 rounded-md bg-secondary/30 border border-border">
                                        <div>
                                            <p className="text-sm font-medium">{visit.purpose}</p>
                                            <p className="text-xs text-muted-foreground">
                                                {new Date(visit.visiteDate).toLocaleDateString()}
                                            </p>
                                        </div>
                                        <div className="text-right">
                                            <span className="text-xs px-2 py-1 rounded-full bg-primary/10 text-primary">
                                                Completed
                                            </span>
                                        </div>
                                    </div>
                                ))
                            ) : (
                                <p className="text-sm text-muted-foreground text-center py-10">No history yet.</p>
                            )}
                        </div>
                    </CardContent>
                </Card>
            </div>
        </div>
    )
}
