"use client"

import { useState, useEffect } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { AreaChart, Area, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from "recharts"
import { ArrowUp, ArrowDown, Users, Clock, Activity } from "lucide-react"

export function AnalyticsView() {
    const [data, setData] = useState<any[]>([])
    const [loading, setLoading] = useState(true)

    // Fetch data (simulated for now, replace with API call later)
    useEffect(() => {
        // Simulate API delay
        setTimeout(() => {
            setData([
                { time: "08:00", visitors: 5, predicted: 4 },
                { time: "09:00", visitors: 12, predicted: 15 },
                { time: "10:00", visitors: 25, predicted: 22 },
                { time: "11:00", visitors: 18, predicted: 25 },
                { time: "12:00", visitors: 15, predicted: 18 },
                { time: "13:00", visitors: 8, predicted: 10 },
                { time: "14:00", visitors: 22, predicted: 25 },
                { time: "15:00", visitors: 30, predicted: 28 },
                { time: "16:00", visitors: 15, predicted: 12 },
                { time: "17:00", visitors: 5, predicted: 6 }
            ])
            setLoading(false)
        }, 1000)
    }, [])

    return (
        <div className="space-y-6 animate-in fade-in zoom-in duration-500">
            <h2 className="text-3xl font-bold tracking-tight text-foreground">Analytics Overview</h2>

            {/* Stats Cards */}
            <div className="grid gap-4 md:grid-cols-3">
                <Card className="bg-card/50 backdrop-blur border-primary/20">
                    <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                        <CardTitle className="text-sm font-medium">Peak Traffic Time</CardTitle>
                        <Clock className="h-4 w-4 text-muted-foreground" />
                    </CardHeader>
                    <CardContent>
                        <div className="text-2xl font-bold">2:00 PM</div>
                        <p className="text-xs text-muted-foreground">Predicted: 3:00 PM tomorrow</p>
                    </CardContent>
                </Card>

                <Card className="bg-card/50 backdrop-blur border-primary/20">
                    <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                        <CardTitle className="text-sm font-medium">Occupancy Rate</CardTitle>
                        <Users className="h-4 w-4 text-muted-foreground" />
                    </CardHeader>
                    <CardContent>
                        <div className="text-2xl font-bold">65%</div>
                        <p className="text-xs text-green-500 flex items-center">
                            <ArrowUp className="h-3 w-3 mr-1" />
                            +12% from last hour
                        </p>
                    </CardContent>
                </Card>

                <Card className="bg-card/50 backdrop-blur border-primary/20">
                    <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                        <CardTitle className="text-sm font-medium">Prediction Accuracy</CardTitle>
                        <Activity className="h-4 w-4 text-muted-foreground" />
                    </CardHeader>
                    <CardContent>
                        <div className="text-2xl font-bold">92%</div>
                        <p className="text-xs text-muted-foreground">AI Confidence Score</p>
                    </CardContent>
                </Card>
            </div>

            {/* Main Chart */}
            <Card className="col-span-4 border-primary/20 shadow-lg">
                <CardHeader>
                    <CardTitle>Visitor Traffic: Actual vs Predicted</CardTitle>
                </CardHeader>
                <CardContent className="pl-2">
                    <div className="h-[350px] w-full">
                        <ResponsiveContainer width="100%" height="100%">
                            <AreaChart data={data}>
                                <defs>
                                    <linearGradient id="colorVisitors" x1="0" y1="0" x2="0" y2="1">
                                        <stop offset="5%" stopColor="hsl(var(--primary))" stopOpacity={0.8} />
                                        <stop offset="95%" stopColor="hsl(var(--primary))" stopOpacity={0} />
                                    </linearGradient>
                                    <linearGradient id="colorPredicted" x1="0" y1="0" x2="0" y2="1">
                                        <stop offset="5%" stopColor="hsl(var(--secondary))" stopOpacity={0.8} />
                                        <stop offset="95%" stopColor="hsl(var(--secondary))" stopOpacity={0} />
                                    </linearGradient>
                                </defs>
                                <XAxis dataKey="time" stroke="#888888" fontSize={12} tickLine={false} axisLine={false} />
                                <YAxis stroke="#888888" fontSize={12} tickLine={false} axisLine={false} tickFormatter={(value) => `${value}`} />
                                <CartesianGrid strokeDasharray="3 3" className="stroke-muted" vertical={false} />
                                <Tooltip
                                    contentStyle={{ backgroundColor: 'hsl(var(--card))', borderRadius: '8px', border: '1px solid hsl(var(--border))' }}
                                    labelStyle={{ color: 'hsl(var(--foreground))' }}
                                />
                                <Area
                                    type="monotone"
                                    dataKey="visitors"
                                    stroke="hsl(var(--primary))"
                                    fillOpacity={1}
                                    fill="url(#colorVisitors)"
                                    name="Actual Visitors"
                                />
                                <Area
                                    type="monotone"
                                    dataKey="predicted"
                                    stroke="hsl(var(--secondary))"
                                    strokeDasharray="5 5"
                                    fillOpacity={1}
                                    fill="url(#colorPredicted)"
                                    name="AI Prediction"
                                />
                            </AreaChart>
                        </ResponsiveContainer>
                    </div>
                </CardContent>
            </Card>
        </div>
    )
}
